.card {
  background: #fff;
  border-radius: 0;
  height: 325px;
  margin: 1rem;
  position: relative;
  z-index: 7;
  width: 325px;
  box-shadow: 0 0px 37px #27ff00, 0 0px 11px #0077ff;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  /*overflow: hidden;*/
  text-align: center;
  background:#202223;
}
.card > .img-container > span > img {
  width: 100%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

.wiggle_and {
    -webkit-animation: shake 0.3s;
            animation: shake 0.3s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

@-webkit-keyframes shake {
    0% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    10% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    20% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    30% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    40% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    50% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    60% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    70% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    80% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    90% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    100% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
}

@keyframes shake {
    0% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    10% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    20% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    30% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    40% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    50% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    60% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    70% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    80% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    90% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    100% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
}
.wrapper {
  background: #000000;
  height: auto;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  background-image: url(/clicky-game/static/media/bg_t1.7603577c.png);
  background-attachment: fixed;
}

.wrapper-spacer {
  width: 100%;
  padding-top: 170px;
}


.bg-black {
  background-color: black;
}

.navbar-ben {
  color: #ffffff;
  position: fixed;
  z-index: 99;
  width: 100%;
  border-bottom: 1px solid green;
  padding: 10px 70px;
}

.message {
  font-family: 'Bangers', cursive;
  font-size: 3rem;
}

.correct {
    color: #0f0;
}

.wrong {
    color: #f00;
}

.navbar-ben ul li.message {
  padding: 0 1rem;
  line-height: 45px;
}

.logo {
  background-image: url(/clicky-game/static/media/Ben_logo.8f190106.png);
  background-repeat: no-repeat;
  width: 100px;
  height: 100px;
  background-size: contain;
  filter: drop-shadow(0px 0px 7px green);
  -webkit-filter: drop-shadow(0px 0px 7px green);
  -moz-filter: drop-shadow(0px 0px 7px green);
  -o-filter: drop-shadow(0px 0px 7px green);
  -ms-filter: drop-shadow(0px 0px 7px green);
  
}



* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

